                    }
                }
            }
        }
    }
    .sidebar .sidebar-menu > ul > li.submenu-open ul li a:hover, .settings-sidebar .sidebar-menu > ul > li.submenu-open ul li a:hover {
        color: $primary;
        span {
            color: $primary; 
        }
        i {
            color: $primary; 
        }
    }
    .sidebar .sidebar-menu > ul li.active svg, .settings-sidebar .sidebar-menu > ul li.active svg {
        color: $primary;   
    }
    .btn-white:hover {
        background: $primary;
        border-color: $primary;
    }
    ::-webkit-scrollbar-thumb {
        background: $primary;
    } 
    .link-item:hover {
        background: rgba($primary, 0.1);
    }
    .link-item:hover .link-icon {
        background: $primary;
    }
