[data-color="orange"]{
    $primary: #155EEF;
    #sales-daychart {
        .apexcharts-bar-series {
            .apexcharts-series {
                &[rel="1"] {
                    path {
                        fill: $primary;
                    }
                }
                &[rel="2"] {
                    path {
                        fill: rgba($primary, 0.4);
                    }
                }
            }
          
        }
    }
    #heat_chart {
        .apexcharts-series {
            rect {
                fill: $primary;
            }
        }
    }
    .btn-group.custom-btn-group .btn.active {
        background: $primary;
        border-color: $primary;
    }
    .category-item.category-primary p::before {
        background: $primary;
    }
    .header-left #toggle_btn {
        background: $primary;
    }
    .sidebar .sidebar-menu > ul > li > ul > li.submenu > a.subdrop, .settings-sidebar .sidebar-menu > ul > li > ul > li.submenu > a.subdrop {
        background: rgba($primary, 0.1);
    }
    .sidebar {
        .sidebar-menu {
            >ul {
                >li {
                    >ul {
                        >li.submenu {
                            >a.subdrop {
                                span {
                                    color: $primary;
                                }
                                i {
                                    color: $primary;
                                }
                            }
                            span.menu-arrow {
                                background: transparent;
